body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol {
  padding: 0px;
}

li {
  list-style-type: none;
  margin-top: 5px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square, .square-highlited{
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square .square-highlited:focus {
  outline: none;
}

.kbd-navigation .square .square-highlited:focus {
  background: #ddd;
}

.square-highlited {
  background: #ADFF2F;
}

.game {
  display: flex;
  flex-direction: column;
}

.game-info {
  margin-left: 20px;
}

.game-board {
  margin-top: 20px;
  display: flex;
  flex-direction: row;
}